.HomeContainer {
    height: 100vh;
    width: 80vw;

    background-color: #073F5A;
    /* background-image: linear-gradient(#1F1C17, #073F5A);  */

    display: flex;
    flex-direction: column;
    align-items: center;
}

.ImageContainer {
    height: 250px;
    width: 250px;

    border: 10px solid #E8E6EA;
    border-radius: 100%;

    overflow: hidden;

    display: flex;
    flex-direction: column;
    justify-content: center;

    margin: 55px 0px 35px 0px;
}

.NameContainer {
    margin: 15px;
}

.FirstName {
    font-size: 3rem;
    text-shadow: 1px 1px 2px #E8E6EA;
}

.LastName {
    font-size: 3rem;
    color: #8CB27F;
    text-shadow: 2px 2px 3px black;
}

.ButtonContainer {
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;

    margin: 25px;
}

.HomeButton {
    width: 165px;
    height: 40px;

    background-color: #55725d;
    color: #E8E6EA;
    border: 2.5px solid #1F1C17;

    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;

    margin: 0px 15px;
}

.DownArrowContainer {
    margin: 25px;
}