.SideBarContainer {
    /* height: 100vh; */
    width: 20vw;
    position: fixed;

    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;

}

.Logo {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;

    margin: 25px 5px 15px 5px;
    font-size: 2rem;

    color: pink;
    text: pink;
}

.MBLOGO {
    color: lime;
    text: lime;
}

.SideBarContainer ul {
    list-style-type: none;
    padding: 0 0 0 0;
    margin: 0;
}

.SideBarContainer li {
    margin: 25px 25px 25px 25px;
}

.SideBarContainer li:hover {
    color: black;
}

.SideBarContainer a {
    text-decoration: none;
    color: #E8E6EA;
}

.SideBarContainer a:hover {
    color: #8CB27F;
}


.SideBarContainer a:active {
    color: red;
}

.HRDivider {
    border-top: 1px solid black;
    border-bottom: 0px solid black;
    border-right: 0px solid black;
    border-left: 0px solid black;
}

.Lines {
    stroke: #1F1C17;
}

.MB {
    stroke: #8CB27F;
    /* stroke: #E8E6EA; */
}